.back_to_top {
  background: url("yancey-official-blog-cat-scroll.png") no-repeat 0 0;
  width: 70px;
  height: 900px;
  position: fixed;
  top: -960px;
  top: -60rem;
  right: 25px;
  -webkit-transition: 600ms all cubic-bezier(0.25, 0.1, 0.3, 1.5);
  -o-transition: 600ms all cubic-bezier(0.25, 0.1, 0.3, 1.5);
  transition: 600ms all cubic-bezier(0.25, 0.1, 0.3, 1.5);
  -webkit-animation: float 2s linear infinite;
          animation: float 2s linear infinite;
  outline: none; }
  .back_to_top:hover {
    cursor: pointer; }

@-webkit-keyframes float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }



